<template>
  <AppBorder :app-info=Login :size=size>
    <template v-slot:header>
      <div class="fx-ac">
        <span>{{ Login.icon }}{{ Login.ZN }}</span>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver loginBox" v-show="isLogin">
        <div class="bak">
        </div>
        <el-form :model="Form" status-icon :rules="rules" ref="Form" label-width="40px"
                 class="demo-Form fx-ver fx-pac">
          <el-form-item label="üìî" prop="account">
            <el-input type="text" v-model="Form.account" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üîí" prop="checkPass">
            <el-input type="password" v-model="Form.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item class="">
            <el-button type="primary" @click="submitForm('Form')">ÁôªÂΩï</el-button>
            <el-button @click="resetForm('Form')">ÈáçÁΩÆ</el-button>
          </el-form-item>
        </el-form>
        <span class="fz14 regBut" @click="switchModel">Ê≥®ÂÜåË¥¶Âè∑</span>
      </div>
      <div v-show="!isLogin" class="fx-ver loginBox">
        <el-form :model="registerForm" status-icon class="demo-Form fx-ver fx-pac"
                 label-width="80px" ref="RegForm" :rules="regRules">
          <el-form-item label="üìîË¥¶Âè∑" prop="username">
            <el-input type="text" v-model="registerForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üîíÂØÜÁ†Å" prop="password">
            <el-input type="password" v-model="registerForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üîíÁ°ÆËÆ§" prop="checkPass">
            <el-input type="password" v-model="registerForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üòÉÊòµÁß∞" prop="nickname">
            <el-input type="text" v-model="registerForm.nickname" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üìßÈÇÆÁÆ±" prop="email">
            <el-input type="text" v-model="registerForm.email" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üì±ÊâãÊú∫" prop="phone">
            <el-input type="text" v-model="registerForm.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="üêµÂ§¥ÂÉè" prop="headImg">
            <el-input type="text" v-model="registerForm.headImg" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item class="">
            <el-button type="primary" @click="submitForm('RegForm')">Ê≥®ÂÜå</el-button>
            <el-button @click="resetForm('RegForm')">ÈáçÁΩÆ</el-button>
          </el-form-item>
        </el-form>
        <span class="fz14 regBut" @click="switchModel">ËøîÂõûÁôªÂΩï</span>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'

export default {
  name: "Login.vue",
  components: {AppBorder},
  data() {
    const validateAccount = (rule, value, callback) => {
      console.log(value)
      if (value === '') {
        callback(new Error('ËØ∑ËæìÂÖ•Ë¥¶Âè∑'));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('ËØ∑ËæìÂÖ•ÂØÜÁ†Å'));
      } else {
        callback();
      }
    };
    const validateCheckPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å'));
      } else {
        if (value !== this.registerForm.password)
          callback(new Error('‰∏§Ê¨°ËæìÂÖ•ÂØÜÁ†Å‰∏çÂêå'));
        else callback();
      }
    };
    return {
      name: "Login",
      Login: {
        name: 'Login',
        isLoad: false,
        min: true,
        icon: 'üë®‚Äç‚öñÔ∏è',
        ZN: 'Áî®Êà∑ÁôªÂΩï'
      },
      Form: {
        account: '',
        checkPass: '',
      },
      rules: {
        account: [
          {required: true, message: 'ËØ∑ËæìÂÖ•Ë¥¶Âè∑', trigger: 'blur'}
        ],
        checkPass: [
          {required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur'}
        ],
      },
      regRules: {
        username: [
          {required: true, message: 'ËØ∑ËæìÂÖ•Ë¥¶Âè∑', trigger: 'blur'},
          {validator: validateAccount, trigger: 'blur'}
        ],
        password: [
          {required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur'},
          {validator: validatePass, trigger: 'blur'}
        ],
        checkPass: [
          {required: true, message: 'ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur'},
          {validator: validateCheckPass, trigger: 'blur'}
        ]
      },
      size: {
        width: '435px',
        height: '315px'
      },
      isLogin: true,
      registerForm: {
        nickname: '',
        username: '',
        password: '',
        checkPass: '',
        email: '',
        headImg: '',
        phone: ''
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (formName === 'Form') this.login();
          else if (formName === 'RegForm') this.register();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    register() {
      this.$http.postBody('/api/register', this.registerForm)
          .then(data => {
            if (data.code === 200) {
              this.$message.success("Ê≥®ÂÜåÊàêÂäüÔºÅ");
              this.switchModel()
            }
          })
          .catch(err => {
            this.$message.error(err);
          });
    },
    login() {
      this.$http.postBody('/api/login', {
        username: this.Form.account,
        password: this.Form.checkPass
      }).then(data => {
        if (data.code === 200) {
          let token = data.msg;
          localStorage.setItem('token', token);
          this.$store.commit("setToken", token);
          this.$message.success("ÁôªÂΩïÊàêÂäüÔºÅ");
          this.$router.push("/desktop");
        } else
          this.$message.error(data.msg)
      }).catch(err => {
        this.$message.error(err);
      });
    },
    closeApp() {
      this.$router.push("/desktop");
    },
    switchModel() {
      if (this.isLogin) {
        this.size.height = '530px';
        this.Login.ZN = 'Áî®Êà∑Ê≥®ÂÜå';
      } else {
        this.size.height = '315px';
        this.Login.ZN = 'Áî®Êà∑ÁôªÂΩï';
      }
      this.isLogin = !this.isLogin;
    }
  }
}
</script>

<style scoped>
.loginBox {
  background-color: white;
  width: 99.5%;
  height: 99%;
  border: 1px saddlebrown solid;
}

.bak {
  width: 100%;
  height: 70px;
  background-color: #73b9fd;
}

.regBut {
  margin: 3px 5px;
  color: #a6a6a6;
  width: 70px;
}

.regBut:hover {
  color: #838383;
}

.el-form-item {
  margin-bottom: 12px;
  margin-top: 10px;
}
</style>
